export default {
  data: function() {
    return {
      clicking: false
    };
  },
  methods: {
    clickBack: function() {
      console.log("12213");
      this.clicking = true;
      this.$router.go(-1);
    },
    clickEnd: function() {
      this.clicking = false;
    }
  }
};



